import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    firstname: "",
    lastname: "",
    mobilenumber: "",
    privacybox: false,
    password: "",
    backupemail: ""
  },
  getters: {
    getDetails: state => {
      return {
        firstname: state.firstname,
        lastname: state.lastname,
        mobilenumber: state.mobilenumber,
        privacybox: state.privacybox,
        password: state.password,
        backupemail: state.backupemail
      };
    }
  },
  mutations: {
    setFirstName: (state, newfirstname) => {
      state.firstname = newfirstname
    },
    setLastName: (state, newlastname) => {
      state.lastname = newlastname
    },
    setMobileNumber: (state, newmobilenumber) => {
      state.mobilenumber = newmobilenumber
    },
    setPrivacyBox: (state, newboxvalue) => {
      state.privacybox = newboxvalue
    },
    setPassword: (state, newpassword) => {
      state.password = newpassword
    },
    setBackupEmail: (state, newbackupemail) => {
      state.backupemail = newbackupemail
    }
  },
  actions: {},
  modules: {}
})