      <!--      <input id="passwordfield" :type="passwordfieldtype" v-model="password" />
      <button @click="switchVisability">show/hide</button>-->
      <passwordfield id="password-form" :passworddata="password" v-model="password"></passwordfield>
      <div class="input-label">CONFIRM PASSWORD</div>
      <passwordfield
        id="confirm-password-form"
        :passworddata="confirmpassword"
        v-model="confirmpassword"
      ></passwordfield>
      <div
        class="input-text"
      >For account recovery purposes, please enter a backup email. Please ensure you verify this account via our confirmation email.</div>
      <div class="input-label">BACKUP EMAIL</div>
      <input type="text" v-model="backupemail " />
    </div>

    <div id="next-button-container">
      <a @click="$router.go(-1)">
        <div id="back-button">Back</div>
      </a>
      <div class="button-space"></div>
      <router-link to="/confirmation">
        <div id="next-button" @click="submitForm(password, backupemail)">Next</div>
      </router-link>
    </div>
  </div>
</template>
<script>
import HighlightingLine from "../components/HighlightingLine.vue";
import stepbartwo from "../components/StepBarTwo.vue";
import passwordfield from "../components/PasswordField.vue";
import { mapMutations } from "vuex";
export default {
  components: { HighlightingLine, stepbartwo, passwordfield },
  data() {
    return {
      password: this.$store.state.password,
      confirmpassword: "",
      backupemail: this.$store.state.backupemail
    };
  },
  methods: {
    ...mapMutations(["setPassword", "setBackupEmail"]),
    submitForm(pass, email) {
      this.setPassword(pass);
      this.setBackupEmail(email);
    }
  }
};
</script>
