import Vue from 'vue'
import VueRouter from 'vue-router'
import Splash from '../views/FW-REG.vue'
import PersonalDetails from '../views/FW-REG.1.vue'
import SMSVerification from '../views/FW-REG.3.vue'
import Password from '../views/FW-REG.6.vue'
import BackupEmailConfirmation from '../views/FW-REG.8.5.vue'
import ProfilePicturePage from '../views/FW-REG.9.vue'
import Review from '../views/FW-REG.12.vue'
import AllSetPage from '../views/FW-REG.13.vue'
import LoginPage from '../views/FW-REG.14.vue'
import PasswordPage from '../views/FW-REG.15.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'splash',
    component: Splash
  },
  {
    path: '/personal_details',
    name: 'personal_details',
    component: PersonalDetails
  },
  {
    path: '/sms_verification',
    name: 'sms_verification',
    component: SMSVerification
  },
  {
    path: '/password_backup',
    name: 'password_backup',
    component: Password
  },
  {
    path: '/confirmation',
    name: 'confirmation',
    component: BackupEmailConfirmation
  },
  {
    path: '/profile_picture',
    name: 'profile_picture',
    component: ProfilePicturePage
  },
  {
    path: '/review',
    name: 'review',
    component: Review
  },
  {
    path: '/all-set',
    name: 'all-set',
    component: AllSetPage
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage
  },
  {
    path: '/password',
    name: 'password',
    component: PasswordPage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "", // active class for non-exact links.
  linkExactActiveClass: "" // active class for *exact* links.
})

export default router
