<template>
  <div class="personal-details-box">
    <stepbarone></stepbarone>
    <HighlightingLine></HighlightingLine>
    <div id="create-account-text">
      <div id="create-account-header">Create your account</div>
      <div id="create-account-body">Enter your first and last name and mobile number.</div>
    </div>
    <section id="inputform">
      <div id="first-name-container">
        <div class="input-label">FIRST NAME</div>
        <input id="first-name" type="text" v-model="firstname" />
      </div>
      <div id="last-name-container">
        <div class="input-label">LAST NAME</div>
        <input id="second-name" type="text" v-model="lastname" />
      </div>
      <div class="input-label">MOBILE NUMBER</div>
      <!--    <input id="mobile-number" type="text" v-model="mobilenumber" /> -->
      <vue-tel-input
        id="mobile-number"
        placeholder
        :validCharactersOnly="true"
        v-model="mobilenumber"
      ></vue-tel-input>
    </section>
    <div id="privacy-notice">
      Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
      <a
        href="#"
      >Privacy Notice</a>
    </div>
    <div id="privacy-clickbox">
      <input type="checkbox" v-model="privacybox" /> GDPR Notice
    </div>
    <div id="next-button-container">
      <div id="next-button" @click="checkData()">Next</div>
    </div>
  </div>
</template>
<script>
import HighlightingLine from "../components/HighlightingLine.vue";
import stepbarone from "../components/StepBarOne.vue";
import { mapMutations } from "vuex";
import { VueTelInput } from "vue-tel-input";
export default {
  components: { HighlightingLine, stepbarone, VueTelInput },

  data() {
    return {
      firstname: this.$store.state.firstname,
      lastname: this.$store.state.lastname,
      mobilenumber: this.$store.state.mobilenumber,
      privacybox: this.$store.state.privacybox,
      formFull: false
    };
  },
  methods: {
    ...mapMutations([
      "setFirstName",
      "setLastName",
      "setMobileNumber",
      "setPrivacyBox"
    ]),
    checkData() {
      if (
        this.firstname != "" &&
        this.lastname != "" &&
        this.mobilenumber != "" &&
        this.privacybox != false
      ) {
        this.formFull = true;
      }
      if (this.formFull === true) {
        this.submitData();
        this.$router.push("sms_verification");
      } else {
        alert("Error - Require All Fields");
      }
    },

    submitData() {
      this.setFirstName(this.firstname);
      this.setLastName(this.lastname);
      this.setMobileNumber(this.mobilenumber);
      this.setPrivacyBox(this.privacybox);
    }
  }
};
</script>
