  width: 373px;
  margin-bottom: 36px;
  #create-account-header {
    @include Framewrk-Heading-01-Default-28353D;
    justify-self: left;
    margin-bottom: 4px;
  }
  #create-account-body {
    @include Framewrk-Body-01-Default-28353D;
  }
}

#inputform {
  @include Framewrk-LABEL-01-DEFAULT-28353D;
  display: flex;
  flex-wrap: wrap;
  width: 373px;
  .input-label {
    margin-bottom: 8px;
    margin-left: 3px;
  }
  #first-name-container {
    margin-right: 15px;
    margin-bottom: 25px;
  }
  #second-name,
  #first-name {
    width: 161px;
    @include Framewrk-Body-01-Default-28353D;
  }

  #mobile-number {
    margin-bottom: 12px;
  }

  input {
    height: 50px;
    border-radius: 4px;
    border: solid 1px $mercury-60;
    padding-left: 16px;
  }
  .vue-tel-input {
    width: 373px;
    height: 50px;
  }
  .vti__dropdown {
    width: 100px;
  }
}

#privacy-notice {
  @include Framewrk-Body-03-Default-28353D;
  width: 373px;
  margin-bottom: 16px;
  a {
    @include Framewrk-Body-03-Link-5BA42B;
  }
}
#privacy-clickbox {
  @include Framewrk-Body-03-Default-28353D;
  width: 380px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  input {
    width: 18px;
    height: 18px;
    margin-right: 16px;
    //   margin-bottom: 16px;
  }
}
#next-button-container {
  width: 373px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 16px;
  #next-button {
    width: 64px;
    height: 36px;
    border-radius: 4px;
    background-color: $sushi-60;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Heebo;
    font-size: 14px;
