<template>
  <div>
    <input
      class="password-field"
      autocorrect="off"
      autocapitalize="off"
      spellcheck="false"
      :type="passwordFieldType"
      v-model="password"
      v-on:input="updateValue($event.target.value)"
    />
    <img
      class="show-hide-icon"
      src="..\assets\show.svg"
      @click="switchVisability"
      v-if="visableIcon"
    />
  </div>
</template>

<script>
export default {
  name: "passwordfield",
  props: {
    passworddata: String
  },
  data() {
    return {
      passwordFieldType: "password",
      password: this.passworddata,
      visableIcon: true
    };
  },
  methods: {
    switchVisability() {
      this.passwordFieldType =
        this.passwordFieldType === "password" ? "text" : "password";
    },
    updateValue(value) {
      this.$emit("input", value);
    },
    setIcon(value) {
      this.visableIcon = value;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/_colors.scss";
@import "../styles/_fonts.scss";

.password-field {
  box-sizing: border-box;
  height: 50px;
  width: 373px;
  border-radius: 4px;
  border: solid 1px $mercury-60;
  @include Framewrk-Body-01-Default-28353D;
  padding-left: 16px;
  &:focus {
    outline: none;
    border: solid 1.5px $sushi-60;
  }
}

.show-hide-icon {
  float: right;
  position: relative;
  z-index: 2;
  margin-top: -32px;
  margin-right: 10px;
}
</style>